@media all and (min-width: 500px) {
  .wrapper {
    display: flex;
  }
}

.container {
  background: url('./assets/6704_b.png') no-repeat;
}

.todo {
  background: url('./assets/6704_y.png') no-repeat;
}

.todo_container {
  width: 100%;
  margin: '0 auto';
  display: flex;
  flex-direction: column;

}







.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
